@charset "UTF-8";
@import "reset/reset.css";
@import url("https://fonts.cdnfonts.com/css/steppe-trial");
@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap");
.header-content {
  display: flex;
  align-items: center;
  font-family: "Raleway", sans-serif !important;
  font-weight: 400;
  letter-spacing: 0.3px;
}
.header-content .nav {
  display: flex;
  gap: 40px;
  margin-right: auto;
}
.header-content .logo {
  display: inline-block;
  margin: 0 auto;
  display: flex;
}
.header-content .logo img {
  max-width: 100%;
}
.header-content .nav-icons {
  margin-left: auto;
  display: flex;
  gap: 15px;
}

.intro {
  padding-top: 90px;
}
.intro .text-nav {
  display: flex;
  flex-direction: column;
  gap: 32px;
}
.intro .btn {
  background-color: #fff !important;
  border-radius: 0;
  padding: 19px 41px;
  transition: none !important;
  color: initial !important;
}
.intro .btn :hover {
  color: black !important;
}
.intro .intro__text-below {
  font-weight: 400;
}
.intro .intro__text {
  font-size: 64px;
  font-weight: 500;
}
.intro .big-img {
  position: absolute;
  text-align: center;
}
.intro .img__text {
  position: relative;
  top: 380px;
  text-align: center;
  color: #EFF7F5;
}
.intro .img__text .img__text-title {
  font-size: 28px;
  margin-bottom: 6px;
  font-weight: 400;
}
.intro .img__text .img__text-text {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  letter-spacing: 0.3px;
}

.img-3 {
  position: relative;
  height: auto; /* Установите высоту, равную высоте изображения */
}

.big-img {
  width: 100%; /* Ширина изображения 100% от родительского блока */
  height: auto; /* Автоматическая высота, чтобы сохранить пропорции изображения */
  position: relative;
}

.main {
  background-color: #fff;
  padding-top: 20px;
}

.card-body {
  color: #004F44 !important;
  font-size: 20px;
  font-weight: 400;
  text-align: center;
}

.card__btn {
  border-radius: none;
  color: #fff !important;
  background-color: #004F44;
  padding: 7px 6px;
}

.ship {
  padding-top: 50px;
}
.ship .shipping__card img {
  width: 100%;
  height: 100%;
}
.ship .ship__card-text {
  margin-left: 55px;
  font-weight: 400;
  color: #9C6955;
}
.ship .ship__card-title {
  font-size: 44px;
}
.ship .ship__card-title-below {
  font-size: 17px;
}
.ship .ship__card-contacts {
  margin-top: 60px;
  display: flex;
  gap: 5px;
  flex-direction: column;
}
.ship .ship__card-contacts .phone::before {
  content: url("../../img/phone-ico.svg");
  margin-right: 5px;
  vertical-align: middle;
}
.ship .ship__card-contacts .email::before {
  content: url("../../img/email-ico.svg");
  margin-right: 5px;
  vertical-align: middle;
}

.main-content-catalog {
  color: black;
}
.main-content-catalog .catalog-start {
  margin-top: 15px;
}
.main-content-catalog .catalog-start .catalog__item-card {
  margin-top: 5px;
}
.main-content-catalog .catalog-start .card-desc {
  display: flex;
  justify-content: space-between;
}
.main-content-catalog .catalog-start .categories {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.main-content-catalog .catalog-start .categories .category {
  color: rgb(112, 112, 112);
  font-size: 30px;
  font-weight: 400;
}

.registration-form {
  color: black;
}
.registration-form .btn-login {
  color: #EFF7F5;
  background-color: #004F44;
}
.registration-form .login-heading {
  font-size: 40px;
  font-weight: 700;
}

.reg-text {
  color: rgb(185, 185, 185) !important;
}

.cart {
  color: #004F44;
  margin-top: 15px;
  width: 100%;
  height: 100%;
  place-items: center;
}
.cart .cart__title {
  font-weight: 700;
  font-size: 36px;
}
.cart .trashbin::before {
  content: url("../../img/trash.svg");
  margin-right: 5px;
  vertical-align: middle;
}
.cart .cart__card:not(:first-child) {
  margin-top: 20px;
}
.cart .cart__btn {
  color: white;
  background-color: #004F44;
  width: 100%;
  padding: 10px 0;
}
.cart .cart__func {
  margin-top: 20px;
}

.container-cart {
  max-width: 750px;
  margin: 0 auto;
}

.card-body {
  font-size: 16px;
}
.card-body .card__text {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.about-us {
  color: black;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

*::after,
*::before {
  box-sizing: inherit;
}

a {
  text-decoration: none !important;
  color: inherit !important;
}

body {
  font-size: 16px;
  color: white;
  font-family: "Raleway", sans-serif !important;
  letter-spacing: 0.3px;
}

ul, li {
  list-style-type: none;
}

.header, .intro {
  background-color: #004F44;
}

.main__title {
  font-size: 64px;
  font-weight: 700;
  color: #004F44;
}