.intro{
    padding-top: 90px;

    .text-nav{
        display: flex;
        flex-direction: column;
        gap: 32px
    }
    .btn{
        background-color: #fff !important;
        border-radius: 0;
        padding: 19px 41px;
        transition: none !important;
        color: initial !important;
        :hover{
            color: black !important;
         }
    }
    .intro__text-below{
        font-weight: 400;
    }
    .intro__text{
        font-size: 64px;
        font-weight: 500;
    }
    .big-img{
        position: absolute;
        text-align: center;
    
    }
    .img__text{
        position: relative;
        top: 380px; 
        text-align: center;
        color: $white-text; 
        .img__text-title{
            font-size: 28px;
            margin-bottom: 6px;
            font-weight: 400;
        }
        .img__text-text{
            font-size: 14px;
            line-height: 18px;
            font-weight: 400;
            letter-spacing: 0.3px;
        }
    }
}
.img-3 {
    position: relative;
    height: auto; /* Установите высоту, равную высоте изображения */
  }
  
  .big-img {
    width: 100%; /* Ширина изображения 100% от родительского блока */
    height: auto; /* Автоматическая высота, чтобы сохранить пропорции изображения */
    position: relative;
  }