@import "reset/reset.css";
@import "etc/vars.scss";
@import "etc/fonts.scss";
@import "blocks/header.scss";
@import "blocks/intro.scss";
@import "blocks/main.scss";
@import "blocks/ship.scss";
@import "blocks/catalog.scss";
@import "blocks/registration.scss";
@import 'blocks/cart.scss';
@import "blocks/profile.scss";
@import "blocks/about.scss";
html {
    box-sizing: border-box;
  }
  
  * {
    box-sizing: inherit;
  }
  *::after,
  *::before {
    box-sizing: inherit;
  }
  
  a {
    text-decoration: none !important;
    color: inherit !important;
  }
body{ 
    font-size: 16px;
    color: white;
    font-family: 'Raleway', sans-serif !important;
    letter-spacing: 0.3px;
}
ul, li{
   list-style-type:none ;
}
.header, .intro {
    background-color: $green;
}
.main__title{
    font-size: 64px;
    font-weight: 700;
    color: #004F44;
    
}