.main-content-catalog{
    color: black;
    .catalog-start{
        margin-top: 15px;
        .catalog__item-card{
            margin-top: 5px;
        }
        .card-desc{
            display: flex;
            justify-content: space-between;
        }
        .categories{
            display: flex;
            flex-direction: column;
            gap: 20px;
            .category{
                color: rgb(112, 112, 112);
                font-size: 30px;
                font-weight: 400;
            }
        }
    }
}