.ship{
    padding-top: 50px;
    .shipping__card img{
    width: 100%;
    height: 100%;

    }
    .ship__card-text{
        margin-left: 55px;
        font-weight: 400;
        color: #9C6955;
    }
    .ship__card-title{
        font-size: 44px;
        
    }
    .ship__card-title-below{
        font-size: 17px;

    }
    .ship__card-contacts{
        margin-top: 60px;
        display: flex;
        gap: 5px;
        flex-direction: column;
        .phone::before{
            content: url('../../img/phone-ico.svg');
            margin-right: 5px;
            vertical-align: middle;
        }
        .email::before{
            content: url('../../img/email-ico.svg');
            margin-right: 5px;
            vertical-align: middle;
        }
}
}