.header-content{
    display: flex;
    align-items: center;
    font-family: 'Raleway', sans-serif !important;
    font-weight: 400;
    letter-spacing: 0.3px;
    .nav{
        display: flex;
        gap: 40px;
        margin-right: auto;
    }
    .logo{
        display: inline-block;
        margin: 0 auto;
        display: flex;
        img{ 
            max-width: 100%;
        }
    }
    .nav-icons{
        margin-left: auto;
        display: flex;
        gap: 15px
    }
}