.cart{
    color: $green;
    margin-top: 15px;
    width: 100%;
    height: 100%;
    place-items: center;

    .cart__title{
        font-weight: 700;
        font-size: 36px;
    }

    .trashbin::before{
        content: url('../../img/trash.svg');
        margin-right: 5px;
        vertical-align: middle;
    }
    .cart__card{
        &:not(:first-child){
            margin-top: 20px;
        }
    }
    .cart__btn{
        color: white;
        background-color: $green;
        width: 100%;
        padding: 10px 0;
    }
    .cart__func{
        margin-top: 20px;
    }
}
.container-cart{ 
    max-width: 750px;
    margin: 0 auto; 
}